/**
 * Seismic entry point for inputs, which uses <slot>s rather than React
 * props/children. Probs don't use this internally.
 */
import PropTypes from 'prop-types';
import React from 'react';
import { omit, isFunction } from 'lodash';

import Input from './Input';
import { controlledFieldPropTypes } from '@devsnc/sn-controls-common';

import style from './input.scss';

/**
 * Does what you'd expect an input field to do, with a few extra goodies.
 * - `controls`: Arbitrary content that gets inlined with the input. Often
 *   used to render buttons that perfom actions related to the field.
 * - `dropdown`: Content that is displayed only when the field has focus. Can
 *   be used to implement an autocomplete list, date picker, etc.
 *
 * Note that the state for this component must be handled by the parent, just
 * like standard [React controlled components](https://reactjs.org/docs/forms.html#controlled-components).
 */
export default function SNInput(props) {
	const attrs = omit(props, [
		'fetchDeclarativeActions',
		'dependentField',
		'canWrite',
		'dependentTable',
		'displayValue'
	]);
	return (
		<Input {...attrs} componentName="sn-record-input">
			<slot name="controls" />
		</Input>
	);
}
const NOW_TEXTAREA_VALUE_SET = 'NOW_TEXTAREA#VALUE_SET';
const NOW_TEXTAREA_INPUT = 'NOW_TEXTAREA#INPUT';

SNInput.actions = {
	[NOW_TEXTAREA_VALUE_SET]: { stopPropagation: true },
	[NOW_TEXTAREA_INPUT]: { stopPropagation: true }
};

const handleChange = ({
	action: {
		payload: { name, value }
	},
	properties: { onValueChange }
}) => {
	// need to handle withValueChangeCallbacks

	onValueChange({ name, value });
};

const callParentOnChange = ({
	action: {
		payload: { name, fieldValue }
	},
	properties: { onChange }
}) => {
	if (isFunction(onChange)) {
		onChange({ name, value: fieldValue });
	}
};

SNInput.actionHandlers = {
	[NOW_TEXTAREA_VALUE_SET]: handleChange,
	[NOW_TEXTAREA_INPUT]: callParentOnChange
};

SNInput.style = style;
SNInput.propTypes = {
	...controlledFieldPropTypes,

	/**
	 * When rendering a textarea, the number of character rows that should be
	 * displayed.
	 */
	rows: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When rendering a textarea, the number of character columns that should be
	 * displayed.
	 */
	cols: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),

	/**
	 * Wrap rendering a textarea, this specifies how text should be wrapped.
	 */
	wrap: PropTypes.string,

	/**
	 * The type of input to render. Note that while any HTML5 type is valid,
	 * there are other form control components that will extend the default
	 * behavior.  Using these higher-order components is often preferred to an
	 * input with an explicit type.
	 *
	 * Use `type="auto"` to enable the component to intelligently switch between
	 * a regular input and a textarea.
	 */
	type: PropTypes.oneOf([
		'auto',
		'color',
		'email',
		'file',
		'month',
		'number',
		'password',
		'range',
		'submit',
		'tel',
		'text',
		'week',
		'textarea'
	]),

	/**
	 * When provided, aligns the input text to the end of the input.
	 */
	align: PropTypes.oneOf(['end']),

	/**
	 * When rendering numeric inputs, the minimum value that should be permitted.
	 */
	min: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When rendering numeric inputs, the maximum value that should be permitted.
	 */
	max: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When rendering numeric inputs, the minimum distance between 2 valid values.
	 */
	step: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * Maximum number of characters to allow in the input.
	 */
	maxlength: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When true, the input will be focused immediately upon being rendered.
	 */
	autofocus: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),

	/**
	 * When rendering a textarea, whether or not to show a character counter.
	 */
	showCounter: PropTypes.bool,
	/**
	 * When rendering a textarea, whether or not to show the resize handle.
	 */
	resizable: PropTypes.bool,

	/**
	 * Disable the field so it can not be used or tabbed to
	 */
	disabled: PropTypes.bool,

	/**
	 * When provided, adds the sn-record-control variant to render
	 */
	variant: PropTypes.string,

	/**
	 * When provided, allows the parent component to listen in on each input change
	 */
	onChange: PropTypes.func
};
