import react from '@servicenow/ui-renderer-react';

import React from 'react';
import ControlWrapper from '@devsnc/sn-record-control-wrapper';

const { createSlot } = react;

/**
 * Container component allowing multiple inputs (or input-like components)
 * to be grouped together.
 */
export default function SNInputGroup(props) {
	return (
		<ControlWrapper {...props}>
			<div className="sn-control-group">{createSlot(props.children)}</div>
		</ControlWrapper>
	);
}
