import React from 'react';
import { size } from 'lodash';
import makeClass from 'classnames';
import { t } from 'sn-translate';

import { isAttrTrue } from '@devsnc/sn-controls-common';

// When the current character count is within this percentage of the max
// length, we make the counter a more prominent warning.
const CHARACTER_COUNTER_WARNING = 0.8;

export default function CharacterCounter({ value, maxlength, readonly }) {
	maxlength = parseInt(maxlength);

	if (Number.isNaN(maxlength)) {
		return null;
	}

	const charCount = size(value);
	const charsRemaining = maxlength - charCount;

	const isEditable = !isAttrTrue(readonly);

	return (
		<div
			className={makeClass('sn-control-character-counter', {
				warning:
					isEditable &&
					charCount / maxlength >= CHARACTER_COUNTER_WARNING &&
					charsRemaining >= 0,
				error: isEditable && charsRemaining < 0
			})}
			aria-live="polite"
			aria-relevant="text"
			aria-atomic="true"
		>
			{t('Characters left: {0}', charsRemaining)}
		</div>
	);
}
