import PropTypes from 'prop-types';
import React from 'react';
import { isFunction, transform, omit, flow, keys, toString } from 'lodash';
import reactAttrConvert from 'react-attr-converter';
import DumbTextInput from './DumbTextInput';
import CharacterCounter from './CharacterCounter';
import FieldAndDropdownWrapper from '@devsnc/sn-field-and-dropdown-wrapper';

import react from '@servicenow/ui-renderer-react';
import {
	FocusWatcher,
	ShadyDOMEventFixer,
	checkAutofocus,
	transformMessages,
	controlledFieldPropTypes,
	seismicComponentPropTypes,
	isAttrTrue,
	castIfBoolean,
	accessibilityAttributes,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

const normalizePlaceholder = (placeholder, readonly) =>
	placeholder || !isAttrTrue(readonly) ? placeholder : '—';

const { createSlot } = react;
// Prop names that do not get forwarded to child components
const REMOVED_PROPS = [
	'autofocus', // handled in componentDidMount instead, see below
	'onValueChange',
	'onStagedValueChange',
	'analyticsEnabled',
	'analyticsRate',
	'analyticsBlacklist',
	'analyticsId',
	'innerRef',
	'debounceAutosize',
	'dropdown',
	'showDropdownWithoutFocus',
	'reversed',
	'children',
	'invalid',
	'required',
	'label',
	'messages',
	'description',
	'showCounter',
	'onBlur', // handled by FocusWatcher
	'handleOutsideScrollFocus',
	'fallbackDropdownBehavior',
	'ignoreDropdown',
	'functionDisplay',
	'componentRef',
	'componentId',
	'componentName',
	'helpers',
	'hoistAppendTo',
	'ariaLabel',
	'ariaLabelledby',
	'ariaDescribedby',
	'configAria',
	'referenceTable',
	'recordSysId',
	'fieldName',
	'tableName',
	'fieldType',
	'fetchDeclarativeActions',
	'declarativeUiActions',
	'formData',
	'nowHoist',
	'nowHoistAppendTo',
	'nowHoistPrependTo',
	'nowId',
	'nowMode',
	'nowName',
	...keys(seismicComponentPropTypes)
];

import '@servicenow/now-textarea';

export class Input extends React.PureComponent {
	state = {
		hasFocus: false,
		hasHover: false,
		value: this.props.value
	};

	// Dropdowns will initially render hidden so that we can position them
	// without a FOUC. Not using a state variable because we show/hide the DOM
	// element directly without triggering multiple React renders.
	dropdownVisible = false;

	componentDidMount() {
		// Uses the `this.focus()` method of this insta\ce rather than focus method
		// from HTMLElement
		if (this.props.type !== 'textarea') {
			checkAutofocus(this.props.autofocus, this);
		}
	}

	componentWillReceiveProps(nextProps) {
		// When re-rendering from an onChange, it first renders
		// with the old value in nextProps.value and then has a
		// 2nd render with the updated value.
		// Workaround it by skipping setState when we know it's from
		// our internal input onChange
		if (this.changeFromInput) {
			this.changeFromInput = false;
			return;
		}

		if (nextProps.value !== this.state.value) {
			this.setState({
				value: nextProps.value
			});
		}
	}

	setInnerRef = node => {
		if (node) {
			this.inputElement = node;

			let { innerRef } = this.props;

			if (isFunction(innerRef)) {
				innerRef(this.inputElement);
			}
		}
	};

	focus = () => {
		// Ensure focus starts at the end of the input
		const length = this.inputElement.value.length;
		this.inputElement.setSelectionRange(length, length);
		this.inputElement.focus();
	};

	onFocus = event => {
		this.setState({
			hasFocus: true
		});

		if (isFunction(this.props.onFocus)) {
			this.props.onFocus(event);
		}
	};

	onBlur = event => {
		this.setState({
			hasFocus: false
		});

		if (isFunction(this.props.onBlur)) {
			this.props.onBlur(event);
		}
	};

	onMouseOver = event => {
		this.setState({
			hasHover: true
		});

		if (isFunction(this.props.onMouseOver)) {
			this.props.onMouseOver(event);
		}
	};

	onMouseOut = event => {
		this.setState({
			hasHover: false
		});

		if (isFunction(this.props.onMouseOut)) {
			this.props.onMouseOut(event);
		}
	};

	onChange = event => {
		const value = event.target.value;
		this.changeFromInput = true;
		this.setState({ value });
		this.props.onValueChange({
			event,
			value
		});

		if (isFunction(this.props.onChange)) {
			this.props.onChange(event);
		}
	};

	onNDSChange = event => {
		const value = event.composedPath()[0].value;
		this.setState({ value });
		this.props.onValueChange({
			event,
			value
		});

		if (isFunction(this.props.onChange)) {
			this.props.onChange(event);
		}
	};

	renderTextarea = () => {
		const showCounter = isAttrTrue(this.props.showCounter);

		return (
			<now-textarea
				className={this.props.required ? 'is-required' : ''}
				value={toString(this.state.value)}
				placeholder={normalizePlaceholder(
					this.props.placeholder,
					this.props.readonly
				)}
				name={this.props.name}
				label={this.props.label}
				showCounter={showCounter}
				messages={transformMessages(this.props.messages)}
				maxlength={this.props.maxlength}
				required={this.props.required}
				disabled={this.props.disabled}
				readonly={this.props.readonly}
				autofocus={this.props.autofocus}
				invalid={this.props.invalid}
				autoresize
				manage-invalid
				manage-value
				onmouseover={this.onMouseOver}
				onmouseout={this.onMouseOut}
				oninput={this.onNDSChange}
			/>
		);
	};

	render() {
		if (this.props.type === 'textarea') {
			return this.renderTextarea();
		}
		const attrs = flow(
			props => omit(props, REMOVED_PROPS),
			props =>
				transform(props, (result, value, key) => {
					result[reactAttrConvert(key)] = castIfBoolean(value);
				})
		)(this.props);
		attrs.placeholder = normalizePlaceholder(
			attrs.placeholder,
			this.props.readonly
		);

		const showCounter = isAttrTrue(this.props.showCounter);

		let { role } = this.props;
		let accessibilityAttrs = accessibilityAttributes(this.props);

		if (role === 'combobox') {
			role = 'searchbox';
			accessibilityAttrs = omit(accessibilityAttrs, 'aria-haspopup');
		}

		attrs.dir = isAttrTrue(this.props.reversed) ? 'rtl' : null;

		// handle blurring on outside-scroll when using new dropdown behavior + has dropdown
		const handleOutsideScroll =
			!this.props.fallbackDropdownBehavior &&
			(this.props.handleOutsideScrollFocus || !!this.props.dropdown);

		return (
			<FocusWatcher
				onBlur={this.onBlur}
				handleOutsideScroll={handleOutsideScroll}
			>
				{setFocusTarget => (
					<React.Fragment>
						<FieldAndDropdownWrapper
							{...omit(this.props, ['componentName'])}
							dropdown={
								this.state.hasFocus || this.props.showDropdownWithoutFocus
									? this.props.dropdown
									: null
							}
							hasFocus={this.state.hasFocus}
							hasHover={this.state.hasHover}
							dropdownRef={setFocusTarget('dropdown')}
							fallbackDropdownBehavior={this.props.fallbackDropdownBehavior}
						>
							{createSlot(this.props.children, 'left')}
							<ShadyDOMEventFixer>
								<DumbTextInput
									{...accessibilityAttrs}
									{...attrs}
									// STRY4883444: When showing character counter, do not enforce a maxLength
									maxLength={showCounter ? undefined : this.props.maxlength}
									onFocus={this.onFocus}
									role={role || 'textbox'}
									id={this.props.componentId}
									name={this.props.name}
									type={this.props.type}
									value={toString(this.state.value)}
									onChange={this.onChange}
									onMouseOver={this.onMouseOver}
									onMouseOut={this.onMouseOut}
									innerRef={node => {
										setFocusTarget('input')(node);
										this.setInnerRef(node);
									}}
								/>
							</ShadyDOMEventFixer>
							<div className="sn-control-controls">
								{createSlot(this.props.children, 'controls')}
							</div>
						</FieldAndDropdownWrapper>
						{showCounter && (
							<CharacterCounter
								value={this.state.value}
								maxlength={this.props.maxlength}
								readonly={this.props.readonly}
							/>
						)}
					</React.Fragment>
				)}
			</FocusWatcher>
		);
	}
}

Input.propTypes = {
	...controlledFieldPropTypes,

	/**
	 * Content to use as controls (inline buttons)
	 */
	children: PropTypes.node,

	/**
	 * Content to use as dropdown
	 */
	dropdown: PropTypes.oneOfType([PropTypes.bool, PropTypes.element]),

	/**
	 * Content to use as the label
	 */
	label: PropTypes.node,

	/**
	 * When rendering a textarea, the number of character rows that should be
	 * displayed.
	 */
	rows: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),

	/**
	 * When rendering a textarea, the number of character columns that should be
	 * displayed.
	 */
	cols: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),

	/**
	 * Wrap rendering a textarea, this specifies how text should be wrapped.
	 */
	wrap: PropTypes.string,

	/**
	 * The type of input to render. Note that while any HTML5 type is valid,
	 * there are other form control components that will extend the default
	 * behavior.  Using these higher-order components is often preferred to an
	 * input with an explicit type.
	 */
	type: PropTypes.oneOf([
		'color',
		'email',
		'file',
		'month',
		'number',
		'password',
		'range',
		'submit',
		'tel',
		'text',
		'week',
		'textarea'
	]),

	/**
	 * The `role` of the input element
	 */
	role: PropTypes.string,

	/**
	 * When provided, aligns the input text to the end of the input.
	 */
	align: PropTypes.oneOf(['end']),

	/**
	 * When rendering numeric inputs, the minimum value that should be permitted.
	 */
	min: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When rendering numeric inputs, the maximum value that should be permitted.
	 */
	max: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * Disable the field so it can not be used or tabbed to
	 */
	disabled: PropTypes.bool,

	/**
	 * When rendering numeric inputs, the minimum distance between 2 valid values.
	 */
	step: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * Maximum number of characters to allow in the input.
	 */
	maxlength: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

	/**
	 * When true, the input will be focused immediately upon being rendered.
	 */
	autofocus: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),

	/**
	 * When rendering a textarea, whether or not to show a character counter.
	 */
	showCounter: PropTypes.bool,

	/**
	 * Whether FocusWatcher should listen for outside scroll event
	 */
	handleOutsideScrollFocus: PropTypes.bool,

	/**
	 * Handle dropdowns the old way
	 */
	fallbackDropdownBehavior: PropTypes.bool
};

export default withValueChangeCallbacks(Input);
